
import './App.css';
import { useState } from 'react';

function App() {
  const questions=[
    {
      questionsText:'Who is your favourite Actor?',
      answerOptions:[
        {answerText:'Leonardo D Caprio',isCorrect:false},
        {answerText:'Henry Cavill',isCorrect:false},
        {answerText:'Ryan Reynolds',isCorrect:true},
        {answerText:'Chris Evans',isCorrect:false},

      ],
    },
    {
      questionsText:'Your favourite Movie?',
      answerOptions:[
        {answerText:'Intersteller',isCorrect:false},
        {answerText:'Twilight',isCorrect:true},
        {answerText:'Wrong Turn',isCorrect:false},
        {answerText:'Shawshank Redemption',isCorrect:false},

      ],
    },
    {
      questionsText:'Choose One?',
      answerOptions:[
        {answerText:'Music',isCorrect:true},
        {answerText:'Sports',isCorrect:false},
        {answerText:'Movies',isCorrect:false},
        {answerText:'Painting',isCorrect:false},

      ],
    },
    {
      questionsText:'Who is your fav character from GOT?',
      answerOptions:[
        {answerText:'Sansa Stark',isCorrect:false},
        {answerText:'Joffery',isCorrect:false},
        {answerText:'John Snow',isCorrect:false},
        {answerText:'Tyrion Lannister',isCorrect:true},

      ],
    },
    {
      questionsText:'What is your favourite color?',
      answerOptions:[
        {answerText:'Red',isCorrect:false},
        {answerText:'Black',isCorrect:true},
        {answerText:'Green',isCorrect:false},
        {answerText:'Yello',isCorrect:false},

      ],
    },
  ];
  const [currentQuestion,setCurrentQuestion]=useState(0);
  const [showScore,setShowScore]=useState(false);
  const[score,setScore]=useState(0);
const handleAnswerOptionClick=(isCorrect)=>{
  if(isCorrect){
    setScore(score+1);
  }
  const nextQuestion=currentQuestion+1;
  if(nextQuestion<questions.length)
  {
    setCurrentQuestion(nextQuestion);
  }
  else{
    setShowScore(true);
  }
};
  return (
    
    <div className="App">
      
  {showScore?(
    <div className='score-section'>
      You scored {score} out of {questions.length}
      </div>
  ):(
    
    <div className='question-section'>
      <div className='question-count'>
        <span>Question {currentQuestion+1}/{questions.length}</span>
        </div>
        <div className='question-text'>{questions[currentQuestion].questionsText}</div>
        <div className='answer-section'>
          {questions[currentQuestion].answerOptions.map((answerOption)=>(
            <button onClick={()=>handleAnswerOptionClick(answerOption.isCorrect)}>{answerOption.answerText}</button>
          )
          
          )}
    
    </div>
    </div>
    
    )}
    </div>
  );
}

export default App;
